html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Corbel, "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", "DejaVu Sans", "Bitstream Vera Sans", "Liberation Sans", Verdana, "Verdana Ref", sans-serif;
  font-size: 16px;
  background-color: #000;
  color: #ccc;
}
a,
a:visited,
a:focus {
  text-decoration: none;
  color: #fff;
}
a:hover {
  color: #fff;
}
#header {
  text-align: center;
  background-color: #333;
  background: -webkit-linear-gradient(top, #333 5%, #000 95%);
  background: -moz-linear-gradient(top, #333 5%, #000 95%);
  background: -o-linear-gradient(top, #333 5%, #000 95%);
  background: -ms-linear-gradient(top, #333 5%, #000 95%);
  background: linear-gradient(to bottom, #333 5%, #000 95%);
  color: #ccc;
  margin-bottom: 2px;
  width: 100%;
}
#header a {
  color: #ccc;
}
#header a:hover {
  color: #fff;
}
#footer {
  background-color: #333;
  background: -webkit-linear-gradient(top, #000 0%, #333 95%);
  background: -moz-linear-gradient(top, #000 0%, #333 95%);
  background: -o-linear-gradient(top, #000 0%, #333 95%);
  background: -ms-linear-gradient(top, #000 0%, #333 95%);
  background: linear-gradient(to bottom, #000 0%, #333 95%);
}
#footer .copyright {
  font-size: 13px;
  color: #444;
  padding: 5px 10px 0 15px;
}
#footer a {
  color: #666;
}
#footer a:hover {
  color: #fff;
}
#wrap {
  min-height: 100%;
  height: auto !important;
  height: 100%;
  width: 100%;
  margin: 0 auto -30px;
}
#push,
#footer {
  height: 30px;
}
#breadcrumb {
  margin: 0;
  padding: 15px;
}
#breadcrumb li {
  display: inline-block;
  padding: 2px;
}
#breadcrumb li:first-child {
  font-weight: bold;
  font-size: 20px;
}
#breadcrumb li + li:before {
  content: " Â» ";
  color: #444;
  padding: 2px 4px;
}
#subalbums,
#pictures {
  text-align: center;
}
#subalbums li,
#pictures li {
  text-align: left;
  display: inline-block;
  position: relative;
  height: 240px;
  border: none;
}
#subalbums li img,
#pictures li img {
  height: 240px;
}
#subalbums li a,
#pictures li a {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  display: block;
  border-left: 3px solid #000;
  width: 100%;
  height: 240px;
  color: #fff;
}
#subalbums li a .title-wrap,
#pictures li a .title-wrap {
  position: absolute;
  top: 190px;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #000;
  background-color: rgba(0,0,0,0.6);
  background: -webkit-linear-gradient(top, transparent 5%, #000 95%);
  background: -moz-linear-gradient(top, transparent 5%, #000 95%);
  background: -o-linear-gradient(top, transparent 5%, #000 95%);
  background: -ms-linear-gradient(top, transparent 5%, #000 95%);
  background: linear-gradient(to bottom, transparent 5%, #000 95%);
}
#subalbums li a .title-wrap .title,
#pictures li a .title-wrap .title {
  position: absolute;
  bottom: 10px;
  padding: 0 10px 0 15px;
}
#subalbums li a:hover,
#pictures li a:hover,
#subalbums li a:focus,
#pictures li a:focus {
  background-color: rgba(255,255,255,0.1);
}
#picture {
  text-align: center;
}
#picture .picture-wrap {
  position: relative;
}
#picture .prev-link,
#picture .next-link {
  display: block;
  height: 100%;
  width: 50%;
  font-weight: bold;
  font-size: 144px;
  color: transparent;
  -webkit-transition: color 0.3s;
  -moz-transition: color 0.3s;
  -o-transition: color 0.3s;
  -ms-transition: color 0.3s;
  transition: color 0.3s;
}
#picture .prev-link:hover,
#picture .next-link:hover,
#picture .prev-link:focus,
#picture .next-link:focus {
  color: #ccc;
}
#picture .prev-link {
  text-align: left;
  position: absolute;
  left: 0;
  padding-left: 20px;
}
#picture .next-link {
  text-align: right;
  position: absolute;
  right: 0;
  padding-right: 20px;
}
#picture .download-link {
  font-size: 13px;
  color: #444;
  text-align: center;
  z-index: 10000;
}
#picture .download-link:hover,
#picture .download-link:focus {
  color: #666;
}
#picture .picture {
  display: block;
  margin: auto;
}
